.withDropdown__default
{
    position:   relative;
}

.withDropdown__default .withDropdown__dropdown
    {
        position:   absolute;
        left:       0;
        z-index:    999;
    }

.withDropdown__default:not(.withDropdown__open) .withDropdown__dropdown
        {
            display:    none;
        }

.withDropdown__default > *
    {
        margin-bottom:  0;
    }

.withDropdown__position__bottom .withDropdown__dropdown
    {
        bottom:     0;
        -webkit-transform:  translateY( 100% ) translateY( 8px );
                transform:  translateY( 100% ) translateY( 8px );
    }

.withDropdown__position__top .withDropdown__dropdown
    {
        top:        0;
        -webkit-transform:  translateY( -100% ) translateY( -8px );
                transform:  translateY( -100% ) translateY( -8px );
    }
