.dropdown__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.dropdown__default:last-child
    {
        margin-bottom:  0;
    }

.dropdown__default
{
    min-width:  0;
    max-width:  none;

    border:     1px solid rgba(89, 116, 148, 0.12);

    -webkit-box-shadow: 0 8px 40px rgba(89, 116, 148, 0.12);

            box-shadow: 0 8px 40px rgba(89, 116, 148, 0.12);

    background: #ffffff;

    border-radius: 2px;
}

.dropdown__error
{
    border-color:   #ff5460;
}

.dropdown__padding__S
{
    padding:    10px;
}

.dropdown__padding__M
{
    padding:    20px;
}

.dropdown__padding__L
{
    padding:    30px;
}

.dropdown__size__default
{
    width: 100%;
}

.dropdown__size__content
{
    width: auto;
}
